.intro {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 10000;
  overflow: hidden;
  display: grid;
  grid-template-areas: "." "logo" "name" "building";
  grid-template-rows: minmax(24px, 1fr) max-content max-content 2fr;
  justify-items: center;
  align-items: center;
  background-color: #fff;
  background-image: url('./intro.png');
  background-size: cover;
  background-position: 50% 50%;
}
.intro:not(.status--active) {
  transition: opacity 1s ease-in-out, transform 1s ease-in-out;
  opacity: 0;
  transform: translateY(100%) scale(0.5);
}
.intro.status--active {
  opacity: 1;
}
  .intro img {
    display: block;
    max-width: 100%;
  }
  @keyframes intro {
    to {
      transform: scale(1) rotate(0);
      opacity: 1;
    }
  }
  .logo {
    grid-area: logo;
    transform: scale(0.25) rotate(1440deg);
    opacity: 0;
  }
  .intro.status--animating .logo {
    animation-name: intro;
    animation-duration: 4s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
  }
    .logo img {
      max-height: 128px;
    }
  @keyframes name {
    to {
      transform: scale(1) translateY(0);
      opacity: 1;
    }
  }
  .name {
    grid-area: name;
    transform: scale(4) translateY(-50vh);
    opacity: 0;
  }
  .intro.status--animating .name {
    animation-name: name;
    animation-duration: 4s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
  }
    .name img {
      max-height: 64px;
    }
  @keyframes building {
    to {
      transform: scale(1) translateY(0);
    }
  }
  .building {
    grid-area: building;
    align-self: end;
    position: relative;
    width: 100%;
    padding-top: 60.248447204968944099378881987578%;
    transform: scale(0) translateY(100%);
  }
  .intro.status--animating .building {
    animation-name: building;
    animation-duration: 4s;
    animation-timing-function: ease-out;
    animation-fill-mode: forwards;
  }
    .building img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }